import marked from 'marked';

marked.setOptions({
   gfm: true,
   tables: true,
   breaks: true,
   pedantic: false,
   sanitize: true,
   smartLists: true,
   smartypants: false,
})



const parseMarkdownToHtml = (content, idContainer) => {
    return document.getElementById(idContainer).innerHTML = marked(content)
}


export default parseMarkdownToHtml