import { useState, useEffect } from 'react';
import parseMarkdownToHtml from './components/parseMarkdownToHtml';
import './index.css';
import marked from 'marked';
import statusInitial from './components/statusInitial.md'

const App = () => {
   const [data, setData] = useState('');
   const [stateInitial, setStateInitial] = useState({
       "md":null,
       "text":null
   })
   const [brillo, setBrillo] = useState(false);

   const onChange = (e) => {
    setData(e.target.value);
    setStateInitial({...stateInitial, "text":null})
   }


  const handleBrillo = () =>{
    setBrillo(!brillo)
  }

  useEffect(() => {
    fetch(statusInitial)
    .then(response => {
        return response.text();
    })
    .then(text => {
        return setStateInitial({...statusInitial, "text":text});
    })

    fetch(statusInitial)
        .then(response => {
            return response.text();
        })
        .then(text => {
            setStateInitial({...statusInitial, "md":marked(text)});
        })
  }, [])
  
  useEffect(() => {
    parseMarkdownToHtml(data, 'preview')
  }, [data])
  
  const handleDelet = () => {
    setStateInitial({
        "md":null,
        "text":null
    });
    setData('')
  }

return(
    
<div className={brillo ? 'onBrillo' : 'offBrillo'}>
    <header>
        <h3 className='delet' onClick={handleDelet}>Delete All</h3>
<h3 className='brillo'onClick={handleBrillo}>{brillo ? <i class="far fa-lightbulb"></i> : <i class="fas fa-lightbulb"></i>}</h3>
    </header>

'    <div id="editors" >
'        <h3 className={`h3 ${brillo ? 'modoOscuro' : null}`}>Editor</h3>
        <textarea id="editor" value={stateInitial.text !== null ? stateInitial.text : data} onChange={onChange}>Hola</textarea>
        
    </div>

    <div id="preview-container">
            <h3 className={brillo ? 'modoOscuro' : null}>Previewer</h3>
            <div id="data">
                <p id="preview" dangerouslySetInnerHTML={data ? null :{__html: stateInitial.md}}>
                </p>
            </div>
        
    </div>
    
</div>

)};

export default App;

